.image{
  width: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  &.image--40{
    max-width: 40px;
    min-width: 40px;
  }
  &:before{
    content:"";
    margin-top: 100%;
    display: block;
  }
  &.image--round{
    border-radius: 100%;
  }
  &.image--2_1{
    &:before{
      margin-top: 50%;
    }
  }
  &.image--4_3{
    &:before{
      margin-top: 75%;
    }
  }
  &.image--3_4{
    &:before{
      margin-top: 133.33%;
    }
  }
  &.image--16_9{
    &:before{
      margin-top: 56.25%;
    }
  }
}
