.background-image-container {
  *{
    z-index: 10;
    position: relative;
  }
  .layout{
    position: absolute;
  }
  &.br{
    .background-image{
      @extend .br;
    }
  }
  &.brt{
    .background-image{
      @extend .brt;
    }
  }
  &.brr{
    .background-image{
      @extend .brr;
    }
  }
  &.brb{
    .background-image{
      @extend .brb;
    }
  }
  &.brl{
    .background-image{
      @extend .brl;
    }
  }
}
.background-image-container:not(.layout){
  position: relative;
  overflow: hidden;

}

.background-image{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-size: cover;
  background-position: center;
}
