/*	Resets
	------	*/
html, body{
  margin: 0;
  padding: 0;
  border: 0;
	font-size: v(typography, main-font-size);
  font-family: v(typography, main-font-family);
  @include smoothing();
  overflow-x: hidden;
  @media only screen and (max-width: 30rem) {
    font-size: calc(#{v(typography, main-font-size)} - 2px);
  }
}
body{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-overflow-scrolling: touch;
}

div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, hr, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figure, figcaption, hgroup, menu, footer, header, nav, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
}
article, aside, canvas, figure, figure img, figcaption, hgroup, footer, header, nav, section, audio, video {
  display: block;
}

*{
  font-weight: normal;
  outline: none;
  box-sizing: border-box;
}
a{
  display: inherit;
  text-decoration: none;
}
