.button{
  position: relative;
  z-index: 10;

  @extend .dif;
  @extend .aic; //vertical
  @extend .jcc; //horizontal
  vertical-align: top;

  -moz-appearance: none;
  -webkit-appearance: none;

  font-size: v(button,font-size);
  line-height: v(button,font-size);
  @extend .mff;
  font-weight: v(button,font-weight);
  font-style: v(button,font-style);
  text-transform: v(button,text-transform);
  letter-spacing: v(button,letter-spacing);
  text-decoration: none;

  min-height: v(button,height);
  height: v(button,height);
  cursor: pointer;
  border: none;

  @extend .ph;
  @extend .br;
  @extend .button--primary;

  transition: all v(transition,duration) v(transition,easing);

  [class^="icon-"], [class*=" icon-"]{
    @extend .mrt;
    font-size: calc( #{v(button,font-size)} * #{v(icon,ratio)} );
    vertical-align: middle;
  }

  &.button--icon-right{
    [class^="icon-"], [class*=" icon-"]{
      @extend .mlt;
      @extend .mrn;
    }
  }
  &.button--icon-alone{
    [class^="icon-"], [class*=" icon-"]{
      @extend .mrn;
    }
  }


  &.button--fluid{
    width: 100%;
  }

  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.button--#{$color} {
        @extend .#{$color}-contrast;
        @extend .bg-#{$color};
        &:hover, &:focus, &.active{
          @extend .bg-#{$color}-variant;
          &:active{
            @extend .bg-#{$color};
          }
        }
        &[class^="bg-gradient"],
        &[class*="bg-gradient"]{
          @extend .bg-#{$color}-variant;
          &:hover, &:focus, &.active{
            background-image: none;
            &:active{
              background-image: inherit;
            }
          }
        }
      }
    }
  }
}


//Hacks

button::-moz-focus-inner {
	border: 0;
	padding: 0;
}
