.nav{
  &.nav--invert{
    .nav__item{
      @extend .op-6;
      transition: opacity v(transition,duration) v(transition,easing);
      &.active, &:hover{
        @extend .op-10;
      }
    }
    @each $color, $value in map-get($vars,color){
      @if (str-index(#{$color}, '-variant')){
      }@elseif (str-index(#{$color}, '-contrast')){
      }@else{
        &.nav--#{$color} {
          .nav__item{
            @extend .#{$color}-contrast;
          }
          &.nav--button, &.nav--tab{
            .nav__item{
              @extend .op-10;
              @extend .#{$color}-contrast;
              &.active, &:hover{
                @extend .bg-#{$color}-variant;
              }
            }
          }
        }
      }
    }
  }
}
