details{
  summary{
    position: relative;
    z-index: 10;
    @extend .dif;
    @extend .aic; //vertical
    @extend .jcc; //horizontal
    vertical-align: top;
    -moz-appearance: none;
    -webkit-appearance: none;
    font-size: v(nav,font-size);
    line-height: v(nav,font-size);
    @extend .mff;
    font-weight: v(nav,font-weight);
    font-style: v(nav,font-style);
    text-transform: v(nav,text-transform);
    letter-spacing: v(nav,letter-spacing);
    text-decoration: none;
    min-height: v(nav,height);
    height: v(nav,height);
    cursor: pointer;

    [class^="icon-"], [class*=" icon-"]{
      @extend .mrt;
      font-size: calc( #{v(nav,font-size)} * #{v(icon,ratio)} );
      vertical-align: middle;
    }

    &::-webkit-details-marker{
      display: none;
    }
    .details-icon-off,
    .details-icon-on{
      display: none;
    }
  }
  &[open]{
    summary{
      .details-icon-on{
        display: block;
      }
    }
  }
  &:not([open]){
    summary{
      .details-icon-off{
        display: block;
      }
    }
  }

  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.details--#{$color} {
        summary{
          @extend .#{$color};
        }
      }
    }
  }
}
