.input{
  width: 100%;
  label{
    display: block;
    margin-bottom: calc( #{v(size,space)} / 4 );
    font-size: v(input,label-font-size);
    line-height: calc( #{v(input,label-font-size)} + 4px );
    @extend .mff;
    font-weight: v(input,label-font-weight);
    font-style: v(input,label-font-style);
    text-transform: v(input,label-text-transform);
    letter-spacing: v(input,label-letter-spacing);
    @extend .dark;
    @each $color, $value in map-get($vars,color){
      @if (str-index(#{$color}, '-variant')){
      }@elseif (str-index(#{$color}, '-contrast')){
      }@else{
        &.label--#{$color} {
          @extend .#{$color};
        }
      }
    }
    &.label--small{
      font-size: calc( #{v(input,label-font-size)} - 4px );
    }
  }

  input, textarea, select {
    font-size: v(input,font-size);
    line-height: 1.5;
    @extend .mff;
    font-weight: v(input,font-weight);
    font-style: v(input,font-style);
    text-transform: v(input,text-transform);
    letter-spacing: v(input,letter-spacing);

    @extend .dif;
    @extend .aic; //vertical
    @extend .jcc; //horizontal
    vertical-align: top;

    -moz-appearance: none;
    -webkit-appearance: none;

    min-height: v(input,height);
    @extend .br;
    @extend .bd;
    background-color: transparent;
    width: 100%;
    box-sizing: inherit;

    transition: border-color v(transition,duration) v(transition,easing), box-shadow v(transition,duration) v(transition,easing);

  }

  input, select{
    @extend .phs;
  }

  textarea{
    @extend .pas;
  }

  select{
    cursor: pointer;

    // For firefox  \_()_/
    &:-moz-focusring {
      color: transparent;
      text-shadow: 0 0 0 #000;
    }
    option{
      min-height: v(input,height);
    }
  }

  input[type="range"]{
    @extend .pan;
    border: none;
    background-color: transparent;
    height: v(input,height);
    width: 100%;
    &::-webkit-slider-runnable-track{
      border-radius: v(border,radius);
    }
  }

  input[type="color"]{
    padding: 0;
    height: v(input,height);
    cursor: pointer;
  }

  input[type="checkbox"]{
    -moz-appearance: checkbox;
    -webkit-appearance: checkbox;
  }

  input[type="radio"]{
    -moz-appearance: radio;
    -webkit-appearance: radio;
  }


  &[class^="input--icon"], &[class*=" input--icon"]{
    position: relative;
    [class^="icon-"], [class*=" icon-"]{
      pointer-events: none;
      font-size: calc( #{v(button,font-size)} * #{v(icon,ratio)} );
      position: absolute;
      bottom: 0;
      min-height: v(input,height);
      min-width: v(input,height);
      line-height: 1.5;
      @extend .df;
      @extend .aic;
      @extend .jcc;
    }
  }
  &.input--icon-left{
    input, select{
      padding-left: v(input,height);
    }
  }
  &.input--icon-right{
    [class^="icon-"], [class*=" icon-"]{
      right: 0;
    }
    input, select{
      padding-right: v(input,height);
    }
  }

  &.input--checkbox,
  &.input--radio{
    @extend .hovcp;
    @extend .dif;
    @extend .aic;
    label{
      @extend .mbn;
      @extend .hovcp;
      @extend .dif;
      @extend .aic;
      transition: all v(transition,duration) v(transition,easing);
      &:before{
        content: "";
        width: v(input, checkbox-size);
        height: v(input, checkbox-size);
        @extend .br;
        @extend .bd;
        @extend .mrs;
        transition: all v(transition,duration) v(transition,easing);
      }
    }
    input[type="checkbox"], input[type="radio"]{
      display: none;
    }
  }
  &.input--radio{
    label:before{
      border-radius: 100%;
    }
  }


  @extend .input--grey-light;

  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.input--#{$color} {
        label{
          @if $color == 'light'{
            @extend .light;
          }
        }
        &.input--checkbox,
        &.input--radio{
          @if $color == 'grey-light'{
            input[type="checkbox"]:checked + label:before,
            input[type="radio"]:checked + label:before{
              @extend .bg-primary;
              @extend .bd-primary;
            }
          } @else if $color == 'grey-medium'{
            input[type="checkbox"]:checked + label:before,
            input[type="radio"]:checked + label:before{
              @extend .bg-primary;
              @extend .bd-primary;
            }
          } @else if $color == 'grey-dark'{
            input[type="checkbox"]:checked + label:before,
            input[type="radio"]:checked + label:before{
              @extend .bg-primary;
              @extend .bd-primary;
            }
          } @else{
            input[type="checkbox"]:checked + label:before,
            input[type="radio"]:checked + label:before{
              @extend .bg-#{$color};
              @extend .bd-#{$color};
            }

          }
          label:before{
            @extend .bd-#{$color};
          }

        }
        [class^="icon-"], [class*=" icon-"] {
          color: v(color,grey-medium);
          @if $color == 'grey-light'{
            color: v(color,grey-medium);
          }  @else if $color == 'grey-medium'{
            color: v(color,grey-dark);
          } @else if $color == 'grey-dark'{
            color: v(color,grey-dark);
          } @else {
            color: v(color,#{$color});
          }
        }
        input, textarea, select {
          @extend .bd-#{$color};

          @if $color == 'primary'{
            &:hover,
            &:focus{
              @extend .bd-secondary;
            }
          } @else {
            &:hover,
            &:focus{
              @extend .bd-primary;
            }
          }
          @if $color == 'grey-light'{
            @extend .dark;
            &::placeholder {
              color: v(color,grey-medium);
            }
          }  @else if $color == 'grey-medium'{
            @extend .dark;
            &::placeholder {
              color: v(color,grey-dark);
            }
          } @else if $color == 'grey-dark'{
            @extend .dark;
            &::placeholder {
              color: v(color,grey-dark);
            }
          } @else {
            @extend .#{$color};
            &::placeholder {
              color: v(color,#{$color});
            }
          }
        }
        input[type="range"]{
          &::-webkit-slider-runnable-track{
            background-color: v(color,#{$color});
          }
        }
      }
    }
  }
}
