
//background color
@each $color, $value in map-get($vars,color){
  @if (str-index(#{$color}, '-variant')){
  }@elseif (str-index(#{$color}, '-contrast')){
  }@else{
    &.bg-#{$color}{
      &.bg-gradient-left {
        background-image: linear-gradient(to right, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-right {
        background-image: linear-gradient(to left, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-top {
        background-image: linear-gradient(to bottom, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-top-left {
        background-image: linear-gradient(to right bottom, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-top-right {
        background-image: linear-gradient(to left bottom, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-bottom {
        background-image: linear-gradient(to top, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-bottom-left {
        background-image: linear-gradient(to right top, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
      &.bg-gradient-bottom-right {
        background-image: linear-gradient(to left top, v(color,#{$color}), v(color,#{$color+'-variant'}));
      }
    }
  }
}
