.input{
  &.input--small{
    &[class^="input--icon"], &[class*=" input--icon"]{
      [class^="icon-"], [class*=" icon-"]{
        font-size: calc( (#{v(button,font-size)} * #{v(icon,ratio)}) / 1.25);
        min-height: calc(#{v(input,height)} / 1.25);
      }
    }
    input, textarea, select {
      font-size: calc(#{v(input,font-size)} / 1.25);
      min-height: calc(#{v(input,height)} / 1.25);
    }
  }
}
