.card{
  &.card--outline{
    @extend .bd;
    .card__image{
      margin-top: minus(#{v(border,size)});
      margin-left: minus(#{v(border,size)});
      .image{
        width: calc(100% + (#{v(border,size)}));
      }
    }
  }

  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.card--#{$color}{
        &.card--outline{
          @if $color == 'light'{
            @extend .bd-grey-light;
          } @else {
            @extend .bd-#{$color}-variant;
          }
        }
      }
    }
  }
}
