.input:not(.input--inside){

  &.input--underline{
    label{
      margin-bottom: 0;
    }
    input, textarea, select {
      @extend .phn;
      border-radius: 0;
      border-color: transparent;
      border-bottom: 0;
    }
    &[class^="input--icon"], &[class*=" input--icon"]{
      [class^="icon-"], [class*=" icon-"]{
        min-width: calc(#{v(input,height)} / 1.5);
        @extend .jcs;
      }
    }
    &.input--icon-left{
      input, select{
        padding-left: calc(#{v(input,height)} / 1.5);
      }
    }
    &.input--icon-right{
      [class^="icon-"], [class*=" icon-"]{
        @extend .jce;
      }
      input, select{
        padding-right: calc(#{v(input,height)} / 1.5);
      }
    }
  }

  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.input--#{$color} {
        &.input--underline{
          input, textarea, select {
            border-color: transparent;
            border-right-width: 0;
            border-left-width: 0;
            box-shadow: 0 v(border,size) 0 0 v(color,#{$color});

            @if $color == 'primary'{
              &:hover,
              &:focus{
                box-shadow: 0 v(border,size) 0 0 v(color,secondary);
              }
            } @else {
              &:hover,
              &:focus{
                box-shadow: 0 v(border,size) 0 0 v(color,primary);
              }
            }
          }
        }
      }
    }
  }
}
