.button-group{
  @extend .df;
  @extend .jcs;
  .button:not(:first-child):not(:last-child){
    border-radius: 0;
    position: relative;
  }
  .button:not(:first-child){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .button:not(:last-child){
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .button:not(:first-child):not(.button--outline){
    &:after{
      content: "";
      position: absolute;
      top: 0;
      left: minus(#{v(border,size) / 2});
      bottom: 0;
      width: v(border,size);
      background-color: v(color,dark);
      @extend .op-2;
    }
  }
  .button--outline:not(:first-child){
    border-left: 0;
  }
  &.button-group--fluid{
    width: 100%;
    .button{
      width: 100%;
    }
  }
}
