.table{
  width: 100%;
  @extend .br;
  @extend .bd;
  @extend .bd-grey-light;
  @extend .mff;
  thead, tfoot{
    @extend .bg-grey-light;
    @extend .grey-dark;
    th:not(:last-child){
      @extend .bdr;
      @extend .bd-light;
    }
    th{
      font-weight: 900;
    }
  }
  tbody{
    tr:not(:last-child){
      @extend .bdb;
      @extend .bd-grey-light;
    }
    th{
      @extend .bdr;
      @extend .bd-grey-light;
    }
  }
  th{
    @extend .pas;
    text-align: left;
    vertical-align: top;
  }

}
