.button{
  &.button--tiny{
    @extend .pht;
    font-size: calc(#{v(button,font-size)} / 1.6);
    line-height: calc(#{v(button,font-size)} / 1.6);
    min-height: calc(#{v(button,height)} / 1.6);
    height: calc(#{v(button,height)} / 1.6);
    [class^="icon-"], [class*=" icon-"]{
      font-size: calc( ( #{v(button,font-size)} * #{v(icon,ratio)} ) / 1.5 );
    }
  }
}
