.button-check{
  display: inline-block;
  label{
    @extend .button;
  }
  input[type="checkbox"], input[type="radio"]{
    opacity: 0;
    position:absolute;
    &:checked{
      + label{
        @extend .active;
        .check-icon-on{
          display: none;
        }
      }
    }
    &:not(:checked){
      + label{
        .check-icon-off{
          display: none;
        }
      }
    }
  }
}
