.nav{
  &.nav--small{
    .nav__item{
      font-size: calc(#{v(nav,font-size)} / 1.25);
      min-height: calc(#{v(nav,height)} / 1.25);
      height: calc(#{v(nav,height)} / 1.25);
      [class^="icon-"], [class*=" icon-"]{
        font-size: calc( ( #{v(nav,font-size)} * #{v(icon,ratio)} ) / 1.25 );
      }
    }
    &:not(.nav--vertical){
      .nav__item{
        @extend .phs;
      }
    }
  }
}
