.input{
  &.input--inside{
    @extend .br;
    @extend .bd-grey-light;
    @extend .bd;
    transition: border-color v(transition,duration) v(transition,easing), box-shadow v(transition,duration) v(transition,easing);
    label{
      @extend .mbn;
      @extend .phs;
      @extend .pts;
    }
    input, textarea, select{
      border-width: 0;
      border-radius: 0;
      padding-top: 0;
      margin-top: 0;
    }

    @each $color, $value in map-get($vars,color){
      @if (str-index(#{$color}, '-variant')){
      }@elseif (str-index(#{$color}, '-contrast')){
      }@else{
        &.input--#{$color} {
          @extend .bd-#{$color};

          @if $color == 'primary'{
            &:hover,
            &:focus{
              @extend .bd-secondary;
            }
          } @else {
            &:hover,
            &:focus{
              @extend .bd-primary;
            }
          }
          @if $color == 'grey-light'{
            @extend .dark;
          }  @else if $color == 'grey-medium'{
            @extend .dark;
          } @else if $color == 'grey-dark'{
            @extend .dark;
          } @else {
            @extend .#{$color};
          }

        }
      }
    }

  }
}
