.dropdown{
  position: relative;
  .dropdown__content{
    position: absolute;
    left: 0;
    transition: opacity v(transition,duration) v(transition,easing);
    opacity: 0;
    width: 200px;
    @extend .br;
    @extend .pa;
    pointer-events: none;
    top: 100%;
    &:before{
      content: "";
      background-color: transparent;
      position: absolute;
      top: -40px;
      right: -40px;
      bottom: -40px;
      left: -40px;
    }
    &.dropdown__content--align-right{
      left: inherit;
      right: 0;
    }
    &.dropdown__content--right{
      left: 100%;
      top: 0;
      &.dropdown__content--align-top{
        top: inherit;
        bottom: 0;
      }
    }
    &.dropdown__content--left{
      left: inherit;
      right: 100%;
      top: 0;
    }
    &.dropdown__content--top{
      top: inherit;
      bottom: 100%;
    }
  }
  &:hover{
    .dropdown__content{
      opacity: 1;
      pointer-events: auto;
    }
  }
}
