.layout{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: v(color,light);
  overflow-x: hidden;
  overflow-y: hidden;
  transition: all v(transition,duration) v(transition,easing);
  background: transparent;

  > .container, > .container-fluid{ // Fix size
    width: inherit;
  }

  &.layout--with-header{
    top: v(size,header);
  }
  &.layout--with-footer{
    bottom: v(size,footer);
  }

  &[class^="middle-"], &[class*="middle-"]{
    @extend .w100;
    @extend .df;
  }

  &.layout--header, &.layout--footer{
    @extend .middle-xs;
    @extend .w100;
    @extend .df;
    @extend .pan;
    z-index: 999;
  }
  &.layout--header{
    height: v(size,header);
    bottom: initial;
  }
  &.layout--footer{
    height: v(size,footer);
    top: initial;
  }

  &.layout--scroll{
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }



  &[class^="layout--sidebar-left"], &[class*="layout--sidebar-left"]{
    z-index: 9999;
    width: v(size,sidebar-left);
    right: initial;
    transform: translateX(#{minus(v(size,sidebar-left))});
  }
  &[class^="layout--sidebar-right"], &[class*="layout--sidebar-right"]{
    z-index: 9999;
    width: v(size,sidebar-right);
    left: initial;
    transform: translateX(#{v(size,sidebar-right)});
  }

  &.layout--backdrop-sidebar{
    z-index: 9998;
    opacity: 0;
    pointer-events: none;
  }

  &.layout--banner-top,
  &.layout--banner-bottom{
    opacity: 0.9;
    z-index: 99999;
  }
  &.layout--banner-top{
    bottom: initial;
    &.js-banner-hide{
      display: none;
    }
  }
  &.layout--banner-bottom{
    top: initial;
    bottom: 0;
    &.js-banner-hide{
      display: none;
    }
  }

  &.layout--modal{
    z-index: 99999;
    opacity: 0;
    pointer-events: none;
    @extend .df;
    @extend .aic;
    @extend .jcc;
    .layout--modal__content{
      pointer-events: none;
      width: 100%;
      max-height: 100vh;
      height: 100%;
      overflow-x: hidden;
      transition: all v(transition,duration) v(transition,easing);
    }
    &.js-modal-visible{
      opacity: 1;
      .layout--modal__content{
        pointer-events: auto;
      }
    }
    &.layout--modal--bottom{
      .layout--modal__content{
        transform: translateY(100px);
      }
      &.js-modal-visible{
        .layout--modal__content{
          transform: translateY(0);
        }
      }
    }
    &.layout--modal--top{
      .layout--modal__content{
        transform: translateY(-100px);
      }
      &.js-modal-visible{
        .layout--modal__content{
          transform: translateY(0);
        }
      }
    }
    &.layout--modal--right{
      .layout--modal__content{
        transform: translateX(100px);
      }
      &.js-modal-visible{
        .layout--modal__content{
          transform: translateY(0);
        }
      }
    }
    &.layout--modal--left{
      .layout--modal__content{
        transform: translateX(-100px);
      }
      &.js-modal-visible{
        .layout--modal__content{
          transform: translateY(0);
        }
      }
    }
    &.layout--modal--scale{
      .layout--modal__content{
        transform: scale(0.7);
      }
      &.js-modal-visible{
        .layout--modal__content{
          transform: scale(1);
        }
      }
    }
  }

  &.layout--backdrop-modal{
    z-index: 99998;
    opacity: 0;
    pointer-events: none;
  }

  &.layout--split-left{
    right: 50%;
  }
  &.layout--split-right{
    left: 50%;
  }

  &.layout--split-left,
  &.layout--split-right{
    width: 50%;
    @media only screen and (max-width: 640px) {
      &.full-xs{ left: 0; right: 0; width: 100%; }
      &.full-sm{ left: 0; right: 0; width: 100%; }
      &.full-md{ left: 0; right: 0; width: 100%; }
    }
    @media only screen and (max-width: 960px) {
      &.full-sm{ left: 0; right: 0; width: 100%; }
      &.full-md{ left: 0; right: 0; width: 100%; }
    }
    @media only screen and (max-width: 1280px) {
      &.full-md{ left: 0; right: 0; width: 100%; }
    }
  }


  &.layout--with-sidebar-left-xs,
  &.layout--with-sidebar-left-sm,
  &.layout--with-sidebar-left-md,
  &.layout--with-sidebar-left-lg{
    left: 0;
  }
  &.layout--with-sidebar-right-xs,
  &.layout--with-sidebar-right-sm,
  &.layout--with-sidebar-right-md,
  &.layout--with-sidebar-right-lg{
    right: 0;
  }
  &.layout--sidebar-left-xs,
  &.layout--sidebar-left-sm,
  &.layout--sidebar-left-md,
  &.layout--sidebar-left-lg{
    transform: translateX(#{minus(v(size,sidebar-left))});
  }
  &.layout--sidebar-right-xs,
  &.layout--sidebar-right-sm,
  &.layout--sidebar-right-md,
  &.layout--sidebar-right-lg{
    transform: translateX(#{v(size,sidebar-right)});
  }




  @media only screen and (min-width: 40rem) {

    &.layout--with-sidebar-left-xs{
      left: v(size,sidebar-left);
    }
    &.layout--with-sidebar-right-xs{
      right: v(size,sidebar-right);
    }
    &.layout--sidebar-left-xs{
      transform: translateX(0);
    }
    &.layout--sidebar-right-xs{
      transform: translateX(0);
    }
    &.layout--with-sidebar-left-sm,
    &.layout--with-sidebar-left-md,
    &.layout--with-sidebar-left-lg{
      left: 0;
    }
    &.layout--with-sidebar-right-sm,
    &.layout--with-sidebar-right-md,
    &.layout--with-sidebar-right-lg{
      right: 0;
    }
    &.layout--sidebar-left-sm,
    &.layout--sidebar-left-md,
    &.layout--sidebar-left-lg{
      transform: translateX(#{minus(v(size,sidebar-left))});
    }
    &.layout--sidebar-right-sm,
    &.layout--sidebar-right-md,
    &.layout--sidebar-right-lg{
      transform: translateX(#{v(size,sidebar-right)});
    }

    &.layout--modal{
      .layout--modal__content{
        width: 80%;
        height: auto;
      }
    }

  }

  @media only screen and (min-width: 60rem) {
    &.layout--with-sidebar-left-xs,
    &.layout--with-sidebar-left-sm{
      left: v(size,sidebar-left);
    }
    &.layout--with-sidebar-right-xs,
    &.layout--with-sidebar-right-sm{
      right: v(size,sidebar-right);
    }
    &.layout--sidebar-left-xs,
    &.layout--sidebar-left-sm{
      transform: translateX(0);
    }
    &.layout--sidebar-right-xs,
    &.layout--sidebar-right-sm{
      transform: translateX(0);
    }
    &.layout--with-sidebar-left-md,
    &.layout--with-sidebar-left-lg{
      left: 0;
    }
    &.layout--with-sidebar-right-md,
    &.layout--with-sidebar-right-lg{
      right: 0;
    }
    &.layout--sidebar-left-md,
    &.layout--sidebar-left-lg{
      transform: translateX(#{minus(v(size,sidebar-left))});
    }
    &.layout--sidebar-right-md,
    &.layout--sidebar-right-lg{
      transform: translateX(#{v(size,sidebar-right)});
    }

    &.layout--modal{
      .layout--modal__content{
        width: 60%;
      }
    }

  }

  @media only screen and (min-width: 80rem) {

    &.layout--with-sidebar-left-xs,
    &.layout--with-sidebar-left-sm,
    &.layout--with-sidebar-left-md{
      left: v(size,sidebar-left);
    }
    &.layout--with-sidebar-right-xs,
    &.layout--with-sidebar-right-sm,
    &.layout--with-sidebar-right-md{
      right: v(size,sidebar-right);
    }
    &.layout--sidebar-left-xs,
    &.layout--sidebar-left-sm,
    &.layout--sidebar-left-md{
      transform: translateX(0);
    }
    &.layout--sidebar-right-xs,
    &.layout--sidebar-right-sm,
    &.layout--sidebar-right-md{
      transform: translateX(0);
    }
    &.layout--with-sidebar-left-lg{
      left: 0;
    }
    &.layout--with-sidebar-right-lg{
      right: 0;
    }
    &.layout--sidebar-left-lg{
      transform: translateX(#{minus(v(size,sidebar-left))});
    }
    &.layout--sidebar-right-lg{
      transform: translateX(#{v(size,sidebar-right)});
    }

    &.layout--modal{
      .layout--modal__content{
        width: 40%;
      }
    }

  }

}

body{
  &.js-modal-visible{
    .layout{
      &.layout--backdrop-modal{
        opacity: 0.8;
        pointer-events: auto;
        cursor: pointer;
      }
    }
  }
  &.js-sidebar-left-visible{
    .layout{
      &.layout--backdrop-sidebar{
        opacity: 0.8;
        pointer-events: auto;
        cursor: pointer;
      }
      &.layout--sidebar-left-xs,
      &.layout--sidebar-left-sm,
      &.layout--sidebar-left-md,
      &.layout--sidebar-left-lg{
        transform: translateX(0);
      }
    }
  }
  &.js-sidebar-right-visible{
    .layout{
      &.layout--backdrop-sidebar{
        opacity: 0.8;
        pointer-events: auto;
        cursor: pointer;
      }
      &.layout--sidebar-right-xs,
      &.layout--sidebar-right-sm,
      &.layout--sidebar-right-md,
      &.layout--sidebar-right-lg{
        transform: translateX(0);
      }
    }
  }
  &.js-bar-hide{
    overflow: hidden;
    .layout--header-hide{
      transform: translateY(#{minus(v(size,header))});
    }
    .layout--with-header-hide{
      top: 0;
    }
    .layout--footer-hide{
      transform: translateY(#{v(size,header)});
    }
    .layout--with-footer-hide{
      bottom: 0;
    }
  }
}
