.nav{
  &.nav--item-underline{
    .nav__item{
      @extend .bdb;
      border-color: transparent;
    }
    &.nav--vertical{
      .nav__item{
        @extend .bdr;
        border-bottom: none;
        border-color: transparent;
      }
    }
    @each $color, $value in map-get($vars,color){
      @if (str-index(#{$color}, '-variant')){
      }@elseif (str-index(#{$color}, '-contrast')){
      }@else{
        &.nav--#{$color},
        &.nav--vertical.nav--#{$color} {
          .nav__item{
            @if $color == 'grey-light'{
              &.active, &:hover{
                @extend .bd-primary;
              }
            }  @else if $color == 'grey-medium'{
              &.active, &:hover{
                @extend .bd-primary;
              }
            } @else if $color == 'grey-dark'{
              &.active, &:hover{
                @extend .bd-primary;
              }
            } @else {
              &.active, &:hover{
                @extend .bd-#{$color}-variant;
              }
            }
          }
        }
      }
    }
  }
}
