.nav{
  @extend .nav--grey-medium;
  .nav__item{
    position: relative;
    z-index: 10;

    @extend .dif;
    @extend .aic; //vertical
    @extend .jcc; //horizontal
    vertical-align: top;

    -moz-appearance: none;
    -webkit-appearance: none;

    font-size: v(nav,font-size);
    line-height: v(nav,font-size);
    @extend .mff;
    font-weight: v(nav,font-weight);
    font-style: v(nav,font-style);
    text-transform: v(nav,text-transform);
    letter-spacing: v(nav,letter-spacing);
    text-decoration: none;

    min-height: v(nav,height);
    height: v(nav,height);
    cursor: pointer;

    @extend .ph;

    transition: background v(transition,duration) v(transition,easing), border-color v(transition,duration) v(transition,easing), color v(transition,duration) v(transition,easing);

    [class^="icon-"], [class*=" icon-"]{
      @extend .mrt;
      font-size: calc( #{v(nav,font-size)} * #{v(icon,ratio)} );
      vertical-align: middle;
    }

  }


  &.nav--vertical{
    .nav__item{
      @extend .df;
      @extend .jcs;
      @extend .pan;
    }
    &.nav--end{
      .nav__item{
        @extend .jce;
      }
    }
    &.nav--center{
      .nav__item{
        @extend .jcc;
      }
    }
  }


  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.nav--#{$color} {
        .nav__item{
          @extend .#{$color};
          @if $color == 'grey-light'{
            &.active, &:hover{
              @extend .primary;
            }
          }  @else if $color == 'grey-medium'{
            &.active, &:hover{
              @extend .primary;
            }
          } @else if $color == 'grey-dark'{
            &.active, &:hover{
              @extend .primary;
            }
          } @else {
            &.active, &:hover{
              @extend .#{$color}-variant;
            }
          }
        }
      }
    }
  }
}
