.expand{
  max-height: 500px;
  overflow: hidden;
  transition: max-height v(transition,duration) v(transition,easing);
  &:not(.js-expand-visible){
    max-height: 0;
  }
}
.show-expand{
  &:not(.js-expand-visible){
    .show-expand-on{
      display: none;
    }
  }
  &.js-expand-visible{
    .show-expand-off{
      display: none;
    }
  }
}
