
//font
.hff{
  font-family: v(typography,header-font-family), Helvetica, sans-serif;
}
.mff{
  font-family: v(typography,main-font-family), Helvetica, sans-serif;
}

//line height
.lh05{
  line-height: 0.5;
}
.lh10{
  line-height: 1;
}
.lh15{
  line-height: 1.5;
}

//font weight
.fw1{
  font-weight: 100;
}
.fw2{
  font-weight: 200;
}
.fw3{
  font-weight: 300;
}
.fw4{
  font-weight: 400;
}
.fw5{
  font-weight: 500;
}
.fw6{
  font-weight: 600;
}
.fw7{
  font-weight: 700;
}
.fw8{
  font-weight: 800;
}
.fw9{
  font-weight: 900;
}

//Width
.w100{ width: 100%;}

//margin
.man { margin:        0}
.mat { margin:        calc( #{v(size,space)} / 3 )}
.mas { margin:        calc( #{v(size,space)} / 2 )}
.ma  { margin:        v(size,space)}
.mal { margin:        calc( #{v(size,space)} * 2 )}

.mtn { margin-top:    0}
.mtt { margin-top:    calc( #{v(size,space)} / 3 )}
.mts { margin-top:    calc( #{v(size,space)} / 2 )}
.mt  { margin-top:    v(size,space)}
.mtl { margin-top:    calc( #{v(size,space)} * 2 )}

.mrn { margin-right:  0}
.mrt { margin-right:  calc( #{v(size,space)} / 3 )}
.mrs { margin-right:  calc( #{v(size,space)} / 2 )}
.mr  { margin-right:  v(size,space)}
.mrl { margin-right:  calc( #{v(size,space)} * 2 )}

.mbn { margin-bottom: 0}
.mbt { margin-bottom: calc( #{v(size,space)} / 3 )}
.mbs { margin-bottom: calc( #{v(size,space)} / 2 )}
.mb  { margin-bottom: v(size,space)}
.mbl { margin-bottom: calc( #{v(size,space)} * 2 )}

.mln { margin-left:   0}
.mlt { margin-left:   calc( #{v(size,space)} / 3 )}
.mls { margin-left:   calc( #{v(size,space)} / 2 )}
.ml  { margin-left:   v(size,space)}
.mll { margin-left:   calc( #{v(size,space)} * 2 )}

.mvn { @extend .mtn; @extend .mbn; }
.mvt { @extend .mtt; @extend .mbt; }
.mvs { @extend .mts; @extend .mbs; }
.mv  { @extend .mt;  @extend .mb; }
.mvl { @extend .mtl; @extend .mbl; }

.mhn { @extend .mrn; @extend .mln; }
.mht { @extend .mrt; @extend .mlt; }
.mhs { @extend .mrs; @extend .mls; }
.mh  { @extend .mr;  @extend .ml; }
.mhl { @extend .mrl; @extend .mll; }

//Padding
.pan { padding:        0}
.pat { padding:        calc( #{v(size,space)} / 3 )}
.pas { padding:        calc( #{v(size,space)} / 2 )}
.pa  { padding:        v(size,space)}
.pal { padding:        calc( #{v(size,space)} * 2 )}

.ptn { padding-top:    0}
.ptt { padding-top:    calc( #{v(size,space)} / 3 )}
.pts { padding-top:    calc( #{v(size,space)} / 2 )}
.pt  { padding-top:    v(size,space)}
.ptl { padding-top:    calc( #{v(size,space)} * 2 )}

.prn { padding-right:  0}
.prt { padding-right:  calc( #{v(size,space)} / 3 )}
.prs { padding-right:  calc( #{v(size,space)} / 2 )}
.pr  { padding-right:  v(size,space)}
.prl { padding-right:  calc( #{v(size,space)} * 2 )}

.pbn { padding-bottom: 0}
.pbt { padding-bottom: calc( #{v(size,space)} / 3 )}
.pbs { padding-bottom: calc( #{v(size,space)} / 2 )}
.pb  { padding-bottom: v(size,space)}
.pbl { padding-bottom: calc( #{v(size,space)} * 2 )}

.pln { padding-left:   0}
.plt { padding-left:   calc( #{v(size,space)} / 3 )}
.pls { padding-left:   calc( #{v(size,space)} / 2 )}
.pl  { padding-left:   v(size,space)}
.pll { padding-left:   calc( #{v(size,space)} * 2 )}

.pvn { @extend .ptn; @extend .pbn; }
.pvt { @extend .ptt; @extend .pbt; }
.pvs { @extend .pts; @extend .pbs; }
.pv  { @extend .pt;  @extend .pb; }
.pvl { @extend .ptl; @extend .pbl; }

.phn { @extend .prn; @extend .pln; }
.pht { @extend .prt; @extend .plt; }
.phs { @extend .prs; @extend .pls; }
.ph  { @extend .pr;  @extend .pl; }
.phl { @extend .prl; @extend .pll; }

// responsive
@media only screen and (max-width: 40rem) {
  .pan-xs { padding: 0 !important}
}

// display
.db{
  display: block;
}
.dib{
  display: inline-block;
}
.di{
  display: inline;
}
.dif{
  display: inline-flex;
}
.df{
  display: flex;
}

// flex
/// align-items
.ais{
  align-items: flex-start;
}
.aic{
  align-items: center;
}
.aie{
  align-items: flex-end;
}
.aib{
  align-items: baseline;
}
.aist{
  align-items: stretch;
}
/// justify-content
.jcs{
  justify-content: flex-start;
}
.jcc{
  justify-content: center;
}
.jce{
  justify-content: flex-end;
}
.jcsb{
  justify-content: space-between;
}
.jcsa{
  justify-content: space-around;
}
/// flex-direction
.fdc{
  flex-direction: column;
}
.fdcr{
  flex-direction: column-reverse;
}
.fdr{
  flex-direction: row;
}
.fdrr{
  flex-direction: row-reverse;
}

// border
.bdt{
  border-style: solid;
  border-top-width: v(border,size);
}
.bdr{
  border-style: solid;
  border-right-width: v(border,size);
}
.bdb{
  border-style: solid;
  border-bottom-width: v(border,size);
}
.bdl{
  border-style: solid;
  border-left-width: v(border,size);
}
.bd{
  @extend .bdt;
  @extend .bdr;
  @extend .bdb;
  @extend .bdl;
}
.bdh{
  @extend .bdt;
  @extend .bdb;
}
.bdv{
  @extend .bdr;
  @extend .bdl;
}

// Border radius
.br{
  border-radius: v(border,radius);
}
.brt{
  border-top-left-radius: v(border,radius);
  border-top-right-radius: v(border,radius);
}
.brr{
  border-top-right-radius: v(border,radius);
  border-bottom-right-radius: v(border,radius);
}
.brb{
  border-bottom-left-radius: v(border,radius);
  border-bottom-right-radius: v(border,radius);
}
.brl{
  border-top-left-radius: v(border,radius);
  border-bottom-left-radius: v(border,radius);
}

// responsive
@media only screen and (max-width: 40rem) {
  .brn-xs { border-radius: 0 !important;}
}

//Opacity
@for $i from 0 through 10 {
  .op-#{$i}{
    opacity: $i / 10;
  }
}

// Box shadow
.bs{
  box-shadow: 0px 0px #{v(shadow,size)} #{v(shadow,color)};
}
.bss{
  box-shadow: 0px 0px calc(#{v(shadow,size)} / 2) #{v(shadow,color)};
}
.bsl{
  box-shadow: 0px 0px calc(#{v(shadow,size)} * 2) #{v(shadow,color)};
}

// Hover
[class^="hov"], [class*="hov"] {
  transition: all v(transition,duration) v(transition,easing);
}
.hovcp{
  cursor: pointer;
}
///shadow
.hovbs{
  &:hover{
    @extend .bs;
  }
}
.hovbss{
  &:hover{
    @extend .bss;
  }
}
.hovbsl{
  &:hover{
    @extend .bsl;
  }
}
///scale
.hovs{
  &:hover{
    transform: scale(1.05);
  }
}

//active
[class^="act"], [class*="act"] {
  transition: all v(transition,duration) v(transition,easing);
}
///shadow
.actbs{
  &:active{
    @extend .bs;
  }
}
.actbss{
  &:active{
    @extend .bss;
  }
}
.actbsl{
  &:active{
    @extend .bsl;
  }
}
///scale
.acts{
  &:active{
    transform: scale(1.02);
  }
}


// Responsive
// XS
@media only screen and (max-width: 640px) {
  .hide-xs{ display: none !important; }
  .hide-sm{ display: none !important; }
  .hide-md{ display: none !important; }
}
// SM
@media only screen and (min-width: 640px) and (max-width: 959px) {
  .show-xs{ display: none !important; }
  //----------------//
  .hide-sm{ display: none !important; }
  .hide-md{ display: none !important; }
  .hide-lg{ display: none !important; }
}
// MD
@media only screen and (min-width: 960px) and (max-width: 1279px) {
  .show-xs{ display: none !important; }
  .show-sm{ display: none !important; }
  //----------------//
  .hide-md{ display: none !important; }
  .hide-lg{ display: none !important; }
}
// LG
@media only screen and (min-width: 1280px) {
  .show-xs{ display: none !important; }
  .show-sm{ display: none !important; }
  .show-md{ display: none !important; }
  //----------------//
  .hide-lg{ display: none !important; }
}
