.card{
  @extend .br;
  @extend .card--grey-light;

  .card__content{
    @extend .pa;
  }
  .card__header{
    @extend .pa;
    @extend .bdb;
  }
  .card__footer{
    @extend .pa;
    @extend .bdt;
  }
  .card__image{
    line-height: 0;
    @extend .brt;
    .image{
      width: 100%;
      @extend .brt;
      img{
        @extend .brt;
      }
    }
  }

  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.card--#{$color}{
        @extend .bg-#{$color};
        .card__header, .card__footer{
          @if $color == 'light'{
            @extend .bd-grey-light;
          } @else {
            @extend .bd-#{$color}-variant;
          }
        }
      }
    }
  }
}
