.input{
  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.input--#{$color} {
        &.input--invert{
          label{
            @extend .#{$color}-contrast;
          }
          [class^="icon-"], [class*=" icon-"] {
            color: v(color,#{$color}-contrast);
            opacity: 0.6;
          }
          input, textarea, select {
            @extend .bd-#{$color}-contrast;
            @extend .#{$color}-contrast;
            &:hover,
            &:focus{
              @extend .bd-#{$color}-variant;
            }
            &::placeholder {
              color: v(color,#{$color}-contrast);
              opacity: 0.6;
            }
          }
          &.input--underline{
            input, textarea, select {
              box-shadow: 0 v(border,size) 0 0 v(color,#{$color}-contrast);
              &:hover,
              &:focus{
                box-shadow: 0 v(border,size) 0 0 v(color,#{$color}-variant);
              }
            }
          }

          &.input--inside{
            @extend .bd-#{$color}-contrast;
            &:hover,
            &:focus{
              @extend .bd-#{$color}-variant;
            }
          }

        }
      }
    }
  }
}
