.button{
  &.button--large{
    font-size: calc(#{v(button,font-size)} * 1.25);
    min-height: calc(#{v(button,height)} * 1.25);
    height: calc(#{v(button,height)} * 1.25);
    [class^="icon-"], [class*=" icon-"]{
      font-size: calc( ( #{v(button,font-size)} * #{v(icon,ratio)} ) * 1.5 );
    }
  }
}
