button[data-tooltip] {
  overflow: visible;
}
[data-tooltip] {
  position: relative;
  z-index: inherit;
}
[data-tooltip]:before,
[data-tooltip]:after {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
  -khtml-opacity: 0;
  -moz-opacity: 0;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: all 0.18s ease-out 0.18s;
  transition: all 0.18s ease-out 0.18s;
  bottom: 100%;
  left: 50%;
  position: absolute;
  z-index: 999;
  -webkit-transform: translate(-50%, 10px);
  -ms-transform: translate(-50%, 10px);
  transform: translate(-50%, 10px);
  -webkit-transform-origin: top;
  -ms-transform-origin: top;
  transform-origin: top;
}
[data-tooltip]:after {
  content: attr(data-tooltip);
  @extend .bg-grey-dark;
  @extend .br;
  @extend .light;
  @extend .pas;
  white-space: nowrap;
  margin-bottom: 11px;
  @extend .p;
  @extend .p--small;
  letter-spacing: normal;
  text-align: center;
}
[data-tooltip]:before {
  content: "";
  margin-bottom: 5px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 7px 0 7px;
  border-color: v(color,grey-dark) transparent transparent transparent;
}
[data-tooltip]:hover:before,
[data-tooltip][data-tooltip-visible]:before,
[data-tooltip]:hover:after,
[data-tooltip][data-tooltip-visible]:after {
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  filter: alpha(opacity=100);
  -khtml-opacity: 1;
  -moz-opacity: 1;
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
[data-tooltip].font-awesome:after {
  font-family: FontAwesome;
}
[data-tooltip][data-tooltip-break]:after {
  white-space: pre;
}
[data-tooltip-pos="down"]:before,
[data-tooltip-pos="down"]:after {
  bottom: auto;
  left: 50%;
  top: 100%;
  -webkit-transform: translate(-50%, -10px);
  -ms-transform: translate(-50%, -10px);
  transform: translate(-50%, -10px);
}
[data-tooltip-pos="down"]:after {
  margin-top: 11px;
}
[data-tooltip-pos="down"]:before {
  margin-top: 5px;
  margin-bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 7px 6px 7px;
  border-color: transparent transparent v(color,grey-dark) transparent;
}
[data-tooltip-pos="down"]:hover:before,
[data-tooltip-pos="down"][data-tooltip-visible]:before,
[data-tooltip-pos="down"]:hover:after,
[data-tooltip-pos="down"][data-tooltip-visible]:after {
  -webkit-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
[data-tooltip-pos="left"]:before,
[data-tooltip-pos="left"]:after {
  bottom: auto;
  left: auto;
  right: 100%;
  top: 50%;
  -webkit-transform: translate(10px, -50%);
  -ms-transform: translate(10px, -50%);
  transform: translate(10px, -50%);
}
[data-tooltip-pos="left"]:after {
  margin-right: 11px;
}
[data-tooltip-pos="left"]:before {
  margin-right: 5px;
  margin-bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 0 7px 6px;
  border-color: transparent transparent transparent v(color,grey-dark);
}
[data-tooltip-pos="left"]:hover:before,
[data-tooltip-pos="left"][data-tooltip-visible]:before,
[data-tooltip-pos="left"]:hover:after,
[data-tooltip-pos="left"][data-tooltip-visible]:after {
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
[data-tooltip-pos="right"]:before,
[data-tooltip-pos="right"]:after {
  bottom: auto;
  left: 100%;
  top: 50%;
  -webkit-transform: translate(-10px, -50%);
  -ms-transform: translate(-10px, -50%);
  transform: translate(-10px, -50%);
}
[data-tooltip-pos="right"]:after {
  margin-left: 11px;
}
[data-tooltip-pos="right"]:before {
  margin-bottom: 0;
  margin-left: 5px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 6px 7px 0;
  border-color: transparent v(color,grey-dark) transparent transparent;
}
[data-tooltip-pos="right"]:hover:before,
[data-tooltip-pos="right"][data-tooltip-visible]:before,
[data-tooltip-pos="right"]:hover:after,
[data-tooltip-pos="right"][data-tooltip-visible]:after {
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
[data-tooltip-length]:after {
  white-space: normal;
}
[data-tooltip-length="small"]:after {
  width: 80px;
}
[data-tooltip-length="medium"]:after {
  width: 150px;
}
[data-tooltip-length="large"]:after {
  width: 260px;
}
[data-tooltip-length="xlarge"]:after {
  width: 90vw;
}
@media screen and (min-width: 768px) {
  [data-tooltip-length="xlarge"]:after {
    width: 380px;
  }
}
[data-tooltip-length="fit"]:after {
  width: 100%;
}
