.button{
  &.button--outline{
    background-color: transparent;
    @extend .bd;
  }
  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.button--#{$color} {
        &.button--outline{
          @extend .#{$color};
          @extend .bd-#{$color};
          &:hover, &:focus, &.active{
            @extend .#{$color}-contrast;
            @extend .bg-#{$color}-variant;
            @extend .bd-#{$color}-variant;
            &:active{
              @extend .bg-#{$color};
              @extend .bd-#{$color};
            }
          }
        }
      }
    }
  }
}
