.button{
  &.button--transparent{
    background-color: transparent;
    border: none;
    @extend .phn;
    transition: opacity v(transition,duration) v(transition,easing);
  }
  @each $color, $value in map-get($vars,color){
    @if (str-index(#{$color}, '-variant')){
    }@elseif (str-index(#{$color}, '-contrast')){
    }@else{
      &.button--#{$color} {
        &.button--transparent{
          @extend .#{$color};
          &:hover, &:focus, &.active{
            @extend .#{$color}-variant;
            background-color: transparent;
            @extend .op-5;
            &:active{
              @extend .#{$color};
              @extend .op-10;
            }
          }
        }
      }
    }
  }
}
