h1, .h1{
  @extend .hff;
  font-size: v(typography,h1-font-size);
  line-height: v(typography,h1-line-height);
  font-weight: v(typography,h1-font-weight);
  text-transform: v(typography,h1-text-transform);
  font-style: v(typography,h1-font-style);
  @extend .dark;
  &.h1--small{
    font-size: calc(#{v(typography,h1-font-size)} / 1.25);
  }
  &.h1--large{
    font-size: calc(#{v(typography,h1-font-size)} * 1.25);
  }
}

