.nav{
  &.nav--underline{
    position: relative;
    @extend .nav--underline--grey-light;
    &:after{
      content: "";
      position: absolute;
      bottom: 0;
      height: v(border, size);
      left: 0;
      right: 0;
    }
    @each $color, $value in map-get($vars,color){
      @if (str-index(#{$color}, '-variant')){
      }@elseif (str-index(#{$color}, '-contrast')){
      }@else{
        &.nav--underline--#{$color} {
          &:after{
            @extend .bg-#{$color};
          }
        }
      }
    }
  }
}
