//TARDIS.IO Variables

//CSS or SCSS variables
$css-var:                 true !default;

@import "vars";

//Build css variables
@if $css-var == true {
  :root {
    @each $object-name, $object in $vars {
      @each $item-name, $item in $object {
        @if map-has-key($item, ext){
          #{"--"+$object-name+"-"+$item-name}: #{map-get($item, val)+map-get($item, ext)};
        } @else {
          #{"--"+$object-name+"-"+$item-name}: map-get($item, val);
        }

      }
    }

  }
}

//return Value
@function v($object, $var) {
  @if $css-var == false {
    @if map-has-key(map-get(map-get($vars,$object),$var), ext){
      @return #{map-get(map-get(map-get($vars,$object),$var), val)+map-get(map-get(map-get($vars,$object),$var), ext)};
    } @else {
      @return map-get(map-get(map-get($vars,$object),$var), val);
    }
  } @else {
    @return var(#{"--"+$object+"-"+$var});
  }
}
